.tfw-grader {
    padding: 1em;
    border-radius: 1em;
    border: 4px dashed rgba(0,0,0,.5);
}

.tfw-grader > pre {
    margin: 0;
}


.tfw-grader > pre.error {
    margin: 1em 0 0 0;
    color: #f00;
    font-size: 80%;
    line-height: 100%;
}

.tfw-grader > div.ok {
    display: inline-block;
    color: #0e0;
    text-shadow: 1px 1px 0 #000;
    font-weight: bolder;
    margin: 0 0 0 2em;
    transform: translateX(5em);
    opacity: 0;
}

.tfw-grader > div.ok.show {
    transition: all .3s;
    transform: translateX(0);
    opacity: 1;
}

*.wdg {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.depiler2bouts-main {
    display: inline-block;
    border: 1em;
    background: #fff;
    border-radius: 1em;
    margin: 1em;
    padding: 1em;
}

.depiler2bouts-main > div.piece {
    position: relative;
    display: inline-block;
    margin: 0;
    padding: 0;
    width: 40px;
    height: 32px;
    overflow: visible;
    cursor: pointer;
    transition: transform .3s;
    transform: scaleX(1);
}

.depiler2bouts-main > div.piece > div {
    position: absolute;
    width: 32px;
    height: 32px;
    line-height: 32px;
    font-size: 20px;
    font-weight: bolder;
    border: 1px solid #333;
    text-align: center;
    border-radius: 100%;
    background: #ffb;
    overflow: hidden;
}

.depiler2bouts-main > div.piece.flip {
    transition: transform .3s;
    transform: scaleX(0);
}

.depiler2bouts-main > div.piece > div.you {
    background: #0f0;
}

.depiler2bouts-main > div.piece > div.him {
    background: #f00;
}

.depiler2bouts-main > div.piece > div::after {
    content: "";
    position: absolute;
    left: -10px;
    top: -10px;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    background: rgba(255,255,255,.5);
}


.depiler2bouts-main > div.piece::before {
    content: "";
    position: absolute;
    display: block;
    left: 4px;
    top: 1px;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    background-color: rgba(0, 0, 0, 0.25);
    box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.25);
    transform-origin: 50% 100% 0px;
    transform: scaleY(0.4);
}

.depiler2bouts-main > div.tbl {
    display: table;
    width: 100%;
    margin: 16px 0 0 0;
    border-collapse: collapse;
}

.depiler2bouts-main > div.tbl > div {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
    width: 33.33%;
}

.depiler2bouts-main > div.tbl .pts1,
.depiler2bouts-main > div.tbl .pts2 {
    font-size: 48px;
    font-bold: bolder;
    text-shadow: 2px 2px 1px rgba(0,0,0,.5);
}

.depiler2bouts-main > div.tbl .pts1 {
    color: #0f0;
}

.depiler2bouts-main > div.tbl .pts2 {
    color: #f00;
}

.depiler2bouts-main a.button,
.depiler2bouts-main a.button:hover,
.depiler2bouts-main a.button:visited {
    display: inline-block;
    text-decoration: none;
    height: 30px;
    font-size: 14px;
    padding: 0 15px;
    border-radius: 15px;
    background: #9cd;
    color: #000;
}


.depiler2bouts-main div.delta {
    font-weight: 300;
    font-size: 12px;
    color: #000;
    margin: .5em 0 0 0;
}

.depiler2bouts-main div.delta.you {
    color: #0f0;
    font-weight: bolder;
}

.depiler2bouts-main div.delta.him {
    color: #f00;
    font-weight: bolder;
}

div.tp4-bulle-container {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: 0;
    background-color: rgba(0,0,0,.7);
    z-index: 100001;
}

div.tp4-bulle-container a.tp4-button.simple {
    color: #579;
}

div.tp4-bulle {
    position: absolute;
    margin: 0;
    padding: 0;
    opacity: 0;
    transition: 1.5s opacity;
    overflow: visible;
}

div.tp4-bulle > div.frame {
    width: 240px;
    position: absolute;
    left: 0;
}

div.tp4-bulle.top > div.frame {
    top: 0;
}

div.tp4-bulle.bottom > div.frame {
    bottom: 0;
}


div.tp4-bulle.show {
    opacity: 1;
    transition: 1.5s opacity;
}

div.tp4-bulle div.svg {
    position: relative;
    width: 240px;
    height: 40px;
    background-repeat:   no-repeat;
    background-position: top left;
    background-size:     240px 40px;
}

div.tp4-bulle.bottom div.svg {
    background-position: bottom left;
}

div.tp4-bulle.left div.svg {
    background-image:    url(tp4.bulle/tl.svg);
}

div.tp4-bulle.right div.svg {
    background-image:    url(tp4.bulle/tr.svg);
}

div.tp4-bulle.left.bottom div.svg {
    background-image:    url(tp4.bulle/bl.svg);
}

div.tp4-bulle.right.bottom div.svg {
    background-image:    url(tp4.bulle/br.svg);
}

div.tp4-bulle div.content {
    position: relative;
    margin: 0;
    width: 220px;
    padding: 10px;
    border-top: none;
    border-left: 1px solid #000;
    border-right: 1px solid #000;
    border-bottom: 1px solid #000;
    background-color: #eee;
    color: #333;
    font-size: 10pt;
    font-family: sans-serif;
}

div.tp4-bulle.bottom div.content {
    border-top: 1px solid #000;
    border-left: 1px solid #000;
    border-right: 1px solid #000;
    border-bottom: none;
}

div.tp4-bulle.left div.content {
    margin-left: 20px;
}
